@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Nerko+One&display=swap);
body{
    margin: 0;
    background-color: #181616;
    /* background-color: #e0dede; */
    /* background-color: black; */
}
.main{
    width: 80vw;
    /* background-color: #f2f2f2; */
    /* background-color: black; */
    /* background-color: #F76B1C; */
    margin: 0 auto;
    padding-top: 7.5vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.section{
    background-color: #ffd2b8;
    width: 100%;
    margin-top: 5vh;
    color: rgba(0, 0, 0, 0.6);
}

h1, h3 {
    color: rgba(0, 0, 0, 0.6);
}
a{
    /* text-decoration: none; */
    color: currentColor;
}


.navbar{
    background-color: rgba(0, 0, 0,1);
    /* background-color:#101010; */
    color: white;
    height: 7vh;
    width: 100vw;
    margin-top: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    position: fixed;
}

.navbar .navlinks{
    color: whitesmoke;
    padding-right: 2vw;
    font-size: 1.7vw;
    text-decoration: none;
}

@media only screen
    and (max-width: 1024px){
    .navbar{
        /* background-color: red; */
        justify-content: space-around;
    }
    .navbar .navlinks{
        /* color: green; */
        padding-right: 2vw;
        font-size: 5vw;
        text-decoration: none;
    }


}

.home{
    /* background-color: green; */
    margin: 0 auto;
    width: 100%;
    height: 85vh;
    background: linear-gradient(-45deg, #F2D50F, #F76B1C,#DA0641,#FAD961);
	background-size: 400% 400%;
    animation: gradient 12s ease infinite;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}




@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.title{
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: larger;
}
.pic{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    padding-top: 20px;
}

/* carousel */
.carousel{
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 10vh;
    margin-left: 14vw;
    height: 20vh;


}


.home .slide {
    width: 200px;
    height: 200px;
}

.carousel .icon{
    width: 40px;
    /* padding-top: 40px; */
}
.carousel .icon:hover{
    transform: scale(1.2);
    cursor: pointer;
}

.home-buttons {
    width: 20vw;
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
}
.home-buttons a{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 9vw;
    height: 50px;
    color: white;
    background-color: black;
    font-family: 'Montserrat', sans-serif;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: none;
    text-decoration: none;
    text-align: center;
    font-size: 1vw;

}

.home-buttons a:hover{
    transform: scale(1.07);
    background-color: rgba(0, 0, 0, 0);
}

@media only screen
    and (max-width: 1024px){

    #home-buttons{
        width: 60vw;
    }
    #my-pic{
        width: 70vw;
        height: 35vh;
        border-radius: 5vw;
    }
    #btn1, #btn2 {
        width:27vw;
        height: 6vh;
        font-size: 4vw;
    }
    #my-title{
        font-size: 5vw;
        margin-bottom: 0;
    }
    #my-title h1{
        color: rgba(0, 0, 0, 0.75);
        margin-bottom: -40px;
    }
    #icon-home{
        width: 13vw;
    }
}

.about-me{
    padding-top: 10vh;
    height: 80vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
}

.container{
    margin-top: 5vh;
    /* background-color: green; */
    width: 70vw;
    height: 70vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.about-me_pic{
    width: 20vw;
    height: 40vh;
    margin-top: 4vh;
}

.about-me_description{
    width: 20vw;
    height: 20vh;
}

@media only screen
    and (max-width: 1024px){

    .container {
        margin: 0 auto;
        flex-direction: column-reverse;
        margin-top: 0;
        justify-content: center;
    }

    .about-me_pic{
        display: none;
        width: 30%;
        height: 20vh;
        margin-top: 0;
        margin: 0 auto;
    }
    .about-me_description{
        text-align: center;
        font-size: 4.2vw;
        width: 100%;
        height: 20vh;
        margin-bottom: 50vh;
    }
    .about-me h1{
        font-size: 7vw;
    }
}

.contact{
    display: flex;
    flex-direction: column;
    height: 70vh;
    /* justify-content: space-between; */
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    padding-bottom: 15vh;
}

.contact-form{
    display:flex;
    flex-direction: row;
    margin: 3vw;
    justify-content: space-between;
}

.contact-form .info{
    display: flex;
    flex-direction: column;
    height: 30vh;
    justify-content: space-around;
    /* background-color: green; */
}
.contact form{
    /* background-color: yellow; */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
input, textarea {
    background: none;
    border: none;
    border-bottom: 2px #f8ba96 solid;
    width: 300px;
    padding-top: 50px;
    outline: none;
    font-size: large;
    color: rgba(0, 0, 0, 0.5);
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input::-ms-input-placeholder, textarea::-ms-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input::placeholder,textarea::placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
textarea{
    width: 700px;
}

.row{
    width: 700px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

button{
    width: 90px;
    height: 50px;
    background: #F76B1C;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-top: 2vh;
    border: none;
    color: white;
}
button:hover{
    transform: scale(1.03);
    cursor: pointer;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.column{
    display: flex;
    flex-direction: column;
}
.icon img{
    width: 3vw;
}
.resume:hover{
    transform: scale(1.07);
    color: black;
}

.resume a{
    text-decoration: none;
    margin-top: 10vh;
}

@media only screen
    and (max-width: 1024px){
    .contact h1{
        font-size: 7vw;
    }
    .contact-form{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .row{
        flex-direction: column;
        /* background-color: green; */
    }
    input {
        border-bottom: 4px #f58c4f solid;
        width: 50vw;
        padding-top: 4vh;
        outline: none;
        font-size: 5vw;
        margin-left: 10vw;
        color: rgba(0, 0, 0, 0.5);
    }
    input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
        font-size: 5vw;
        text-align: center;
        color: rgba(0, 0, 0, 0.3);
    }
    input:-ms-input-placeholder, textarea:-ms-input-placeholder{
        font-size: 5vw;
        text-align: center;
        color: rgba(0, 0, 0, 0.3);
    }
    input::-ms-input-placeholder, textarea::-ms-input-placeholder{
        font-size: 5vw;
        text-align: center;
        color: rgba(0, 0, 0, 0.3);
    }
    input::placeholder,textarea::placeholder{
        font-size: 5vw;
        text-align: center;
        color: rgba(0, 0, 0, 0.3);
    }
    textarea{
        border-bottom: 4px #f58c4f solid;
        margin-top: 2vh;
        width: 70vw;
        font-size: 5vw;
    }
    button{
        align-self: center;
        width: 30vw;
        height: 5vh;
        font-size: 5vw;
        margin-bottom: -5vh;
        /* background-color: green; */
    }
    .column {
        display: none;
    }
    .resume{
        display: block;
        /* background-color: red; */
        margin-top: 7vh;
        font-size: 4.5vw;
    }
    .icon img{
        width: 100px;
    }

}

.projects{
    display: flex;
    flex-direction: column;
    height: 70vh;
    /* justify-content: space-between; */
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    padding-top: 40px;
    padding-bottom: 15vh;
    color: rgba(0, 0, 0, 0.6);
}

.projects-container{
    width: 80vw;
    margin-top: 10vh;
    /* background-color: greenyellow; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: baseline;
}

.projects-container div{
    width: 17vw;
    height: 30vh;
}


.cards {
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cards .card_img{
    width: 18vw;
    height: 30vh;
    border-radius: 5px;

}

.cards:hover{
    transform: scale(1.1);
    cursor: pointer;
}

.modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
}

.modal_demo #video {
    width: 500px;
    padding: 20px;
}

.modal_img{
    width: 13vw;
    height: 20vh;
    padding: 0.5vw;
}

.modal_img:hover{
    transform: scale(1.05);
}

#links a{
    padding: 40px;
    text-decoration: none;
    margin-top: 20vh;
    /* overflow: hidden; */
    color: black;
    font-size: 20px;
}
#links a:hover{
    color: #F76B1C;
    font-size: 21px;
}

#tech {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 20px;
}
#tech h4{
    margin-bottom: 0;
}
#tech-items{
    display: flex;
    flex-direction: row;
}


.item{
    padding: 0.5vw;
}

#details{
    text-align: center;
    margin-right: 2vw;
    margin-left: 2vw;
}

@media only screen
    and (max-width: 1024px){

    .projects{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
    }
    .projects h1{
        margin-bottom: -5vh;
        font-size: 7vw;
    }
   .projects-container{
       flex-direction: column;
       height: 150vh;
       padding-bottom: 50px;
   }
   .projects-container h3{
       width: 50vw;
       padding-left: 15vw;
       font-size: 6vw;
       margin: 0;
   }
   #proj{
       margin-bottom: 20vh;
       margin: 0 auto;
       margin-left: 2.5vw;
   }
   #proj img{
       width: 75vw;
   }
   #desc{
       text-align: center;
       width: 70vw;
       padding-left: 5vw;
   }
   .desc{
       font-size: 4vw;
       width: 200px;
       text-align: center;
   }

   .modal{
       justify-content: flex-start;
       padding-top: 0vh;
   }

   .modal h1{
       font-size: 7vw;
   }

   #links a{
       font-size: 5vw;
   }

   .modal_demo{
       width: 60vw;
       margin-left: -10vw;
   }
   #details{
       font-size: 3vw;
   }
   #tech{
       width: 60vw;
       height: auto;
       text-align: center;
       display: flex;
       flex-direction: column;
   }
   #tech h4{
       font-size: 4vw;
       margin: 0;
   }
   #tech-items{
       display: flex;
       flex-direction: row;
       flex-wrap: wrap;
       font-size: 4vw;
   }

   .modal_demo #video{
       width: 80vw;
   }

   #links a{
       color: #F76B1C;
       text-decoration: underline;
   }

   .cards:hover{
       transform: none;
   }
}

.skills{
    padding-top: 10vh;
    height: 140vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    color: rgba(0, 0, 0, 0.7);
}

.skills .container {
    /* padding-top: 10vh;
    height: 80vh; */
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    font-family: 'Montserrat', sans-serif;
}

.skills .new-skill{
    padding-top: 20px;
    width: 300px;
    height: 200px;
    /* background-color: green; */
    display: flex;
    flex-direction: column;
    text-align: center;
}

.new-skill img{
    margin: 0 auto;
    width: 100px;
}

@media only screen
    and (max-width: 1024px){
    .skills h1{
        margin: 0;
        font-size: 7vw;
    }
    .skills {
        height: 240vh;
    }

    .container {
        justify-content: center;
        align-items: center;
    }
    .new-skill.node{
        padding-top: 10vh;
    }

    .new-skill.css{
        margin-top: -6vh;
    }
    #skill {
        width: 40vw;
        height: 18vh;
        font-size: 4vw;
    }
}

